import Folder from "./Folder.vue";

export default {
    components: {
        Folder
    },
    props: {
        folders: null,
        blocked: null,
        i18n_labelNeedSync: String
    },
    computed: {
        sortedFolders: function(){
            return this.folders.slice().sort((a, b) => {
                return a.foldername.localeCompare(b.foldername);
            });
        }
    }

};
