import Label from "./Label";

export default {
    components: {
        Label
    },
    props: {
        name: null,
        label: null,
        description: null,
        value: null,
        options: null,
        defaultValue: null,
    },
    data() {
        return {
            currentOption: ""
        };
    },
    watch: {
        value: function(newValue){
            this.currentOption = newValue;
        }
    },
    mounted() {
        this.currentOption = this.options[0].value;
        this.$emit('input', this.currentOption);
    },
    methods: {

    }
};
